<template>
    <home-layout>
        <router-view v-if="alive"/>
        
        <init ref="initREF" v-if="$store.state.chronus"></init>

    </home-layout>
</template>

<script>
import HomeLayout from '@/components/Layout/HomeLayout.vue'
import Chronus from './Chronus/Chronus.vue'
import Init from '../../components/Data/Init.vue'
    export default {
  components: { HomeLayout, Chronus, Init },
        name: '',
        provide() {
            return {
                reload: this.reload
            }
        },
        data() {
            return {
                alive: true
            }
        },
        async mounted() {
            await this.$refs.initREF.init()
        },
        methods: {
            reload() {
                this.alive = false
                this.$nextTick(function() { this.alive = true })
            }
        },  
    }
</script>

<style lang="sass" scoped>
    
</style>


